body,
html {
	width: 100%;
	min-height: 100%;
	margin: 0;
	padding: 0;
}

body {
	font-size: 16px;
	font-family: Roboto, Arial, Helvetica;
	color: $main_txt;
	background: $main_bg;
}

a,
a:hover,
a:focus {
	text-decoration: none;
}

h1 {
	font-size: 34px;
	margin: 10px 0 40px;
}

h3.subtitle {
	font-size: 16px;
	margin: 16px 0;
	font-weight: 400;
}

h2 {
	font-size: 28px;
	text-align: center;
}

p {
	margin: 16px 0;
}

.container-wrapper {
	width: 100%;
	min-height: 100vh;
	margin: 0;
	padding: 0;
	position: relative;
}

.container {
	width: 100%;
	padding: 30px 0;
}

.content-wrapper {
	width: 990px;
	margin-left: auto;
	margin-right: auto;
	padding-left: 15px;
	padding-right: 15px;
}

nav {
	text-align: center;
	a:focus,
	a:hover,
	a {
		padding: 10px 12px;
		background: $main_bg;
		color: $light_txt;
		@include create-round-corners(4px);
		margin: 20px 10px;
		width:120px;
		display: inline-block;
	}
}

@media (min-width: 768px) and (max-width: 980px) {
	.content-wrapper {
		width: 720px;
	}

}

@media (min-width: 351px) and (max-width: 767px) {
	.content-wrapper {
		width: calc(100% - 30px);
	}
}

@media (max-width: 350px) {
	.content-wrapper {
		width: calc(100% - 20px);
		padding-left: 10px;
		padding-right: 10px;
	}
}